import { faArrowLeft } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import html2canvas from "html2canvas";
import { useState, useRef, useEffect } from "react";
import { Helmet } from "react-helmet-async";
import { useNavigate } from "react-router-dom";
import { toast, ToastContainer } from "react-toastify";
import styled from "styled-components";

const Container = styled.div`
  max-width: 440px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: #f8f0e5;
  position: relative;
  padding: 100px 20px;
  h1 {
    text-align: center;
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 50px;
  }
`;

const BackButton = styled.button`
  position: absolute;
  top: 5%;
  left: 5%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: white;
  border: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Frame = styled.div`
  display: grid;
  grid-template-rows: repeat(4, 140px);
  row-gap: 10px;
  width: 252px;
  height: 756px;
  background-color: ${(props) =>
    props.bgColor || "lightgray"}; /*   */
  padding: 10px;
  margin: 0 auto;

  .frame-item {
    height: 140px;
    overflow: hidden;
  }

  img {
    width: 100%;
    object-fit: cover;
  }
`;

const TitleWrap = styled.div`
  width: 100%;
  height: 40px;
  border-radius: 4px;
  padding: 5px;
  display: flex;
  align-items: center;
  color: ${(props) => (props.isLight ? "black" : "white")}; /*   */
`;

const UploadButtonWrap = styled.div`
  margin-top: 20px;
  display: flex;
  justify-content: center;

  button {
    width: 200px;
    padding: 10px 20px;
    background-color: #fff;
    color: black;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    font-size: 16px;
    box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.1);
  }
`;

const Sample = styled.div`
  width: 100%;
  height: 100%;
  background-color: #f0f0f0;
  border: 2px dashed #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: #999;
`;

const DateWrap = styled.div`
  text-align: end;
  /* color: white; */
  color: ${(props) => (props.isLight ? "black" : "white")}; /*   */
`;

const Upload = () => {
  const [title, setTitle] = useState(""); //   
  const navigate = useNavigate();
  const [bgColor, setBgColor] = useState("#ffffff"); //  

  const [images, setImages] = useState([null, null, null, null]);
  const frameRef = useRef(null); // useRef 
  const today = new Date();

  console.log(today.toLocaleDateString()); // : "2024. 12. 16." ( )

  const handleImageUpload = (e) => {
    const files = Array.from(e.target.files); //   
    const newImages = [...images];

    //     
    files.forEach((file, index) => {
      if (index < 4) {
        newImages[index] = URL.createObjectURL(file);
      }
    });

    setImages(newImages.slice(0, 4)); //  4 
  };

  const handleDownload = () => {
    if (frameRef.current) {
      html2canvas(frameRef.current).then((canvas) => {
        const link = document.createElement("a");
        link.download = "storycut.png";
        link.href = canvas.toDataURL("image/png");
        link.click();

        toast.success(" !", {
          position: "bottom-center", //  
          autoClose: 3000, //     ()
        });
      });
    }
  };

  useEffect(() => {
    const savedTitle = localStorage.getItem("storyTitle");
    const savedColor = localStorage.getItem("frameColor");
    if (savedTitle) setTitle(savedTitle);
    if (savedColor) setBgColor(savedColor);
  }, []);

  const handleBack = () => {
    navigate(-1);
  };

  const isLightBackground = bgColor === "#ffffff" || bgColor === "white";

  return (
    <Container>
      <Helmet>
        <title>_</title>
      </Helmet>
      <BackButton onClick={handleBack}>
        <FontAwesomeIcon icon={faArrowLeft} />
      </BackButton>
      <h1>  !</h1>
      <Frame ref={frameRef} bgColor={bgColor}>
        {images.map((src, index) => (
          <div key={index} className="frame-item">
            {/* {src ? <img src={src} alt={`uploaded-${index}`} /> : " "} */}
            {src ? (
              <img src={src} alt={`uploaded-${index}`} />
            ) : (
              <Sample></Sample>
            )}
          </div>
        ))}
        <TitleWrap isLight={isLightBackground}>{title}</TitleWrap>
        <DateWrap isLight={isLightBackground}>
          {today.toLocaleDateString()}
        </DateWrap>
      </Frame>
      <UploadButtonWrap>
        <input
          type="file"
          multiple
          accept="image/*"
          onChange={handleImageUpload}
          style={{ display: "none" }}
          id="upload-input"
        />
        <label htmlFor="upload-input">
          <button
            onClick={() => document.getElementById("upload-input").click()}
          >
             
          </button>
        </label>
      </UploadButtonWrap>
      <UploadButtonWrap>
        <button onClick={handleDownload}></button>
      </UploadButtonWrap>
      <ToastContainer />
    </Container>
  );
};

export default Upload;
